.model-page::before, .model-page::after {
  content: "";
  position: absolute;
  z-index: -1;
  height: 0;
  width: 0;
  transition: all 3s ease;
}

.model-page-0000::before {
  background-color: #e5eaeb;
}

.model-page-0000::after {
  background-color: #ede0df;
}

.model-page-0001 .img {
  position: relative;
}

.model-page-0001 .img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
}

.model-page-0001::before {
  background-color: #e6ffe8;
}

.model-page-0001::after {
  background-color: #ffe7e7;
}

.model-page-0002::before {
  background-color: #c4b7d2;
}

.model-page-0002::after {
  background-color: #fff;
}

.model-page-0003 {
  color: #fbf3f3;
}

.model-page-0003 g {
  fill: #fbf3f3;
}

.model-page-0003 .current-model__pagination-dot {
  color: #fbf3f3;
}

.model-page-0003 .current-model__pagination-dot::before, .model-page-0003 .current-model__pagination-dot::after {
  background-color: #fbf3f3;
}

.model-page-0003 .current-model__arrow {
  color: #fbf3f3;
}

.model-page-0003 .footer-collection__title::before {
  background-color: #fbf3f3;
}

.model-page-0003::before {
  background-color: #720000;
}

.model-page-0003::after {
  background-color: #060606;
}

.model-page-0003 .img {
  position: relative;
}

.model-page-0003 .img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.model-page--vertical::before, .model-page--vertical::after {
  top: 0;
  height: 100%;
  width: 50%;
  animation: defaultParties 1s ease-in forwards;
}

.model-page--vertical::before {
  right: 0;
  transform: translateX(100%);
}

.model-page--vertical::after {
  left: 0;
  transform: translateX(-100%);
}

.model-page--horizontal::before, .model-page--horizontal::after {
  left: 0;
  height: 50%;
  width: 100%;
  transition: all 3s ease;
}

.model-page--horizontal::before {
  bottom: 0;
}

.model-page--horizontal::after {
  top: 0;
}

.model-page--horizontal-mode-1::before, .model-page--horizontal-mode-1::after {
  left: 0;
  width: 100%;
}

.model-page--horizontal-mode-1::before {
  bottom: 0;
  height: 80%;
}

.model-page--horizontal-mode-1::after {
  top: 0;
  height: 20%;
}

.model-page--horizontal-mode-2::before, .model-page--horizontal-mode-2::after {
  left: 0;
  width: 100%;
}

.model-page--horizontal-mode-2::before {
  bottom: 0;
  height: 20%;
}

.model-page--horizontal-mode-2::after {
  top: 0;
  height: 80%;
}

