  .wrapper {
    overflow: scroll;
  }
}

@media (max-width: 1120px) {
  html {
    font-size: 8px;
  }
  .main-collections__inner {
    margin-top: 50px;
  }
  .model-item {
    flex: 1 1 20%;
  }
  .model-item::before {
    bottom: -4%;
    font-size: 16px;
  }
  .row {
    justify-content: center;
  }
  .model-list--right {
    order: 1;
    margin-left: 10.9375rem;
