  }
}

@media (max-width: 815px) {
  .wrapper {
    overflow: scroll;
  }
  .footer {
    margin: 10px 0 10px;
  }
  .header__inner > .header__logo svg {
    margin: 5px 0 5px auto;
    transform: translateX(50%);
  }
  .coll-list {
    display: none;
    position: absolute;
    top: 58px;
    background-color: #fff;
    height: 100vh;
    width: 100%;
    z-index: 100;
    transform: translateX(-200%);
    transition: all 1s ease-in;
  }
  .coll-list--active {
    padding: 30px 0;
    display: block;
    overflow: scroll;
    transform: translateX(0);
  }
  .coll-list--active .model-list {
    display: block;
    margin-left: 0;
  }
  .coll-list--active .model-list li {
    text-align: left;
  }
  .coll-list--active .model-list a {
    padding-left: 100px;
    width: auto;
    font-size: 28px;
  }
  .model-list {
    display: none;
  }
  .model-item {
    flex: 0 1 50%;
  }
}

@media (max-width: 600px) {
  .main-collections__inner {
    margin-top: 0;
  }
  .row {
    flex-direction: column;
  }
  .row:last-child {
    align-items: stretch;
  }
  .main-collections::before {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(90deg);
    width: 0;
    height: 0;
    font-size: 400px;
  }
  .model-item {
    flex: 0 1 100%;
    margin-bottom: 30px;
    padding-left: 30px;
  }
  .model-item__image {
    min-height: 350px;
  }
  .model-item__title {
    font-size: 30px;
  }
  .model-item + .model-item {
    margin-left: 0;
  }
}

@media (max-height: 685px) {
